@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@600;700&display=swap');


*{
  font-family: 'OpenSans', sans-serif;
}


.a {
  width: 20%;
  padding: 20px;
  margin-bottom: 10px;
  background: #00C8F3;
  overflow: hidden;
  position: relative;
  color: #fff;
  border-radius: 20px;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
}

.b {
  width: 20%;
  padding: 20px;
  left: 60%;
  margin-bottom: 10px;
  background: #3FFB7F;
  overflow: hidden;
  position: relative;
  color: #fff;
  border-radius: 20px;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
}

.message{
  width:100%;
}

.input-div{
  position: relative;
  padding: 10%;
  margin:10px;
}

.input{
  width: 70%;
  padding:20px;
  border-radius:10px;
}

.button{
  width: 10%;

  padding:20px;
  border:none;
  background-color:#3F51B5;
  color:#fff;
  border-radius:5px;

}
